.new-post-form {
    display: flex;
    justify-content: center;
    position: relative;
    top:100px;
    margin-bottom: 50px;
}

.input-post-text {
    width: 450px;
    padding: 8px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 8px;
}

.add-post-btn {
    width:35px;
    height: 35px;
    background-color:green;
    border: 0px;
    border-radius: 50%;
    margin-left: 10px;
    font-size: 20px;
    color:white;
    cursor: pointer;
}